"use strict";
var RKBOPView_1 = require("./stageRKB/RKBOPView");
var StageOnlineView_1 = require("./stageOnline/StageOnlineView");
var routes = [
    {
        path: '/', name: 'panel',
        components: { default: RKBOPView_1.rkbView }
    },
    {
        path: '/rkb/:op/:game_id',
        components: { default: RKBOPView_1.rkbView }
    },
    {
        path: '/ol/:op/:game_id',
        components: { default: StageOnlineView_1.StageOnlineView }
    }
];
var router = new VueRouter({
    routes: routes
});
router.afterEach(function (to, from) {
});
var app = new Vue({
    router: router
}).$mount('#panel');



